/* App.css */

/* Google Fonts for better typography */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden; /* Prevent page scrolling */
}

.app.light {
  background-color: #f5f5f5;
  color: #222;
}

.app.dark {
  background-color: #121212;
  color: #fafafa;
}

.scramble {
  font-size: 2.5em;
  font-weight: 700;
  margin-bottom: 30px;
  text-align: center;
  padding: 10px 20px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  box-sizing: border-box;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.timer {
  font-size: 6em;
  font-weight: 700;
  margin-bottom: 30px;
  padding: 20px 40px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  box-sizing: border-box;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.timer:active {
  transform: scale(1.05);
}

.history-container {
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-height: 0; /* Allow the container to shrink */
}

.history-container h2 {
  margin: 0;
  padding: 10px 0;
  text-align: center;
  flex-shrink: 0; /* Prevent h2 from shrinking */
}

.history {
  flex-grow: 1;
  min-height: 0;
  list-style: none;
  padding: 0 10px;
  margin: 0;
  overflow-y: auto; /* Enable vertical scrolling within the history */
}

.history li {
  font-size: 1.2em;
  padding: 10px;
  margin: 5px 0;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  text-align: center;
  box-sizing: border-box;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.history li:hover {
  transform: translateY(-2px);
}
